<!-- Lynn -->
<script>
import TableComponent from "@/components/Table/Table.vue";

export default {
  name: "courseInfoManagement-component",
  components: { TableComponent },
  data() {
    return {
      // Tab1 教室管理
      classroomTableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "room_No", label: "教室編號", width: "" },
        { id: "description", label: "教室名稱", width: "" },
      ],

      addRoomForm: {
        room_No: "", // 教室編號
        description: "", // 廠區代號+教室名稱
        max_Audience: 0,
        deleted: "0",
        room_Id: "",
      },
      addRoomRules: {
        room_No: [{ required: true, message: "必填", trigger: "blur" }],
        description: [{ required: true, message: "必填", trigger: "blur" }],
      },
      updRoomDialog: false,
      updRoomForm: {
        room_No: "", // 教室編號
        description: "", // 廠區代號+教室名稱
        max_Audience: 0,
        deleted: "0",
        room_Id: "",
      },
      updRoomRules: {
        room_No: [{ required: true, message: "必填", trigger: "blur" }],
        description: [{ required: true, message: "必填", trigger: "blur" }],
      },
      classroomPage: 1, // 教室表格的頁數

      // Tab2 課程管理
      allCourseTableColumns: [
        { id: "index", label: "項次", width: "100" },
        { id: "course_No", label: "課程代號", width: "125" },
        { id: "course_Name", label: "課程名稱", width: "375" },
        { id: "min_Audience", label: "最小開課人數", width: "125" },
        { id: "description2", label: "狀態", width: "" },
      ],
      allCoursePage: 1, // 課程表格的頁數

      // Tab3 課表管理
      addScheduleForm: {
        course_Id: "", // 課程清單
        course_No: "", // 課程清單
        course_Description: "", // 課程說明
        special: false, // 是否為專班
        special_Class: "",
        password: "", // 報名密碼
        vendor: [], // 選擇廠商
        room_No: "", // 教室地點
        min_Audience: "", // 最大開課人數(上限)
        stop_Date: "", // 停止報名日期
        stop_Time: "", // 停止報名時間
        repeat_Code: "", // 課程型態
        day_In_Week: "0", // 每週上課時間
        available_begin_date: "", // 課表日期(起)
        available_End_Date: "", // 課表日期(迄)
        begin_Time: "", // 上課時間(起)
        end_Time: "", // 上課時間(迄)
        teacher_Emp_No: "",
        arrange_Emp_No: "",
      },
      vendorLoading: false,
      maxStudent: [15, 20, 30, 35, 40, 50, 60, 70, 80, 100, 120, 140], // 最大開課人數
      addScheduleRules: {
        course_No: [{ required: true, message: "必填", trigger: "change" }],
        room_No: [{ required: true, message: "必填", trigger: "change" }],
        repeat_Code: [{ required: true, message: "必填", trigger: "change" }],
        day_In_Week: [{ required: true, message: "必填", trigger: "change" }],
        available_begin_date: [
          { required: true, message: "必填", trigger: "change" },
        ],
        available_End_Date: [
          { required: true, message: "必填", trigger: "change" },
        ],
        begin_Time: [{ required: true, message: "必填", trigger: "change" }],
        end_Time: [{ required: true, message: "必填", trigger: "change" }],
      },

      courseTableColumns: [
        { id: "index", label: "項次", width: "70" },
        {
          id: "courseInfo",
          label: "課表編號/ 課程名稱/ 課程說明 (課程代號)",
          width: "350",
        },
        { id: "courseType", label: "課程型態(上課時間)", width: "150" },
        {
          id: "courseRoom",
          label: "教室編號/最大開課人數(上限)",
          width: "300",
        },
        { id: "courseTime", label: "日期資訊", width: "180" },
      ],
      coursePage: 1, // 課表表格的頁數
    };
  },
  computed: {
    // 所有教室清單 (All)
    classroom() {
      return this.$store.state.classroom;
    },
    // 所有教室清單 (單頁)
    classroomList() {
      return this.$store.state.classroom_list;
    },
    // 所有課程清單 (All)
    allCourse() {
      return this.$store.state.all_course;
    },
    // 所有課程清單 (單頁)
    allCourseList() {
      return this.$store.state.all_courselist;
    },
    // 教室地點選單
    allClassroom() {
      return this.$store.state.all_classroom;
    },
    // 指定廠商選單
    vendorList() {
      return this.$store.state.vendor_list;
    },
    // 所有課表清單 (All)
    course() {
      return this.$store.state.course;
    },
    // 所有課表清單 (單頁)
    courseList() {
      return this.$store.state.course_list;
    },
  },
  methods: {
    submitAddRoom(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch(
              "addClassroom",
              this.addRoomForm
            );
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "新增成功",
              });

              // 清空表單，並更新教室資料 (呈現第一頁)
              this.$refs[formName].resetFields();
              this.classroomPage = 1;
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async delRoom(item) {
      try {
        const res = await this.$store.dispatch("delClassroom", [item]);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "刪除成功",
          });

          this.classroomPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },

    // 開啟視窗，並代入相應的資訊
    openDialog(item) {
      this.updRoomDialog = true;
      let obj = { ...item };
      this.updRoomForm = obj;
    },
    handleClose(done) {
      this.$confirm("確定關閉？")
        .then(() => {
          this.closeDialog("dialogForm");
          done();
        })
        .catch(() => {});
    },
    closeDialog(formName) {
      this.updRoomDialog = false;
      this.updRoomForm = {};
      this.$refs[formName].clearValidate();
    },
    submitUpdRoom(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch(
              "updClassroom",
              this.updRoomForm
            );
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "修改成功",
              });

              // 關閉視窗，並更新教室資料 (呈現第一頁)
              this.closeDialog(formName);
              this.classroomPage = 1;
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    classroomChangePage(page) {
      this.classroomPage = page;
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "classroom",
        paramsListName: "classroom_list",
      });
    },

    allCourseChangePage(page) {
      this.allCoursePage = page;
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "all_course",
        paramsListName: "all_courselist",
      });
    },

    // 隨機產生 1000~9999
    randomPswdHandler(e) {
      if (e) {
        this.addScheduleForm.password = Math.floor(Math.random() * 9000) + 1000;
      } else {
        this.addScheduleForm.password = "";
      }
    },
    // 按 enter 後蒐取廠商清單
    vendorSearchHandler(evt) {
      this.vendorLoading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.vendorLoading = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getVendorList", evt.target.value);
      }
    },
    repeatCodeHandler(e) {
      if (e == "SINGLE") {
        // 單日課程：不需填寫每週日期、課程結束日
        this.addScheduleForm.day_In_Week = "0";
        this.addScheduleRules.day_In_Week = [];
        this.$refs.addScheduleForm.clearValidate("day_In_Week");
        this.addScheduleForm.available_End_Date = "";
        this.addScheduleRules.available_End_Date = [];
        this.$refs.addScheduleForm.clearValidate("available_End_Date");
      } else {
        // 每週課程：不可申請為專班
        this.addScheduleForm.special_Class = "";
        this.addScheduleForm.password = "";
        this.addScheduleForm.vendor = [];

        this.addScheduleForm.day_In_Week = "";
        this.addScheduleRules.day_In_Week = [
          { required: true, message: "必填", trigger: "change" },
        ];
        this.addScheduleRules.available_End_Date = [
          { required: true, message: "必填", trigger: "change" },
        ];
      }
    },
    submitAddSchedule(formName) {
      if (
        this.addScheduleForm.repeat_Code == "WEEKLY" &&
        this.addScheduleForm.available_begin_date >
          this.addScheduleForm.available_End_Date
      ) {
        this.$message({
          type: "error",
          showClose: true,
          message: "上課日期有誤，請調整",
        });
      } else if (
        this.addScheduleForm.begin_Time >= this.addScheduleForm.end_Time
      ) {
        this.$message({
          type: "error",
          showClose: true,
          message: "上課時間有誤，請調整",
        });
      } else {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            // 填入 course_Id 及專班代碼
            this.addScheduleForm.course_Id = this.allCourse.filter(
              (e) => e.course_No == this.addScheduleForm.course_No
            )[0].course_Id;
            this.addScheduleForm.special_Class = this.addScheduleForm.special
              ? "Y"
              : "";

            // 單日課程：開始日期＝結束日期
            if (!this.addScheduleForm.available_End_Date) {
              this.addScheduleForm.available_End_Date =
                this.addScheduleForm.available_begin_date;
            }
            try {
              const res = await this.$store.dispatch(
                "addSchedule",
                this.addScheduleForm
              );
              if (res.data.isOK) {
                this.$message({
                  type: "success",
                  duration: 5000,
                  showClose: true,
                  message: "新增成功",
                });

                // 清空表單，並更新課表資料 (呈現第一頁)
                this.$refs[formName].resetFields();
                this.coursePage = 1;
              }
            } catch (err) {
              console.log(err);
            }
          } else {
            this.$message({
              type: "error",
              showClose: true,
              message: "資料填寫錯誤",
            });
          }
        });
      }
    },
    courseChangePage(page) {
      this.coursePage = page;
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "course",
        paramsListName: "course_list",
      });
    },

    // 攜帶該課表之資料，進入修改頁面
    EditMain(item) {
      const { href } = this.$router.resolve({
        path: "/scheduleMainEdit",
        query: {
          data: JSON.stringify(item),
        },
      });
      window.open(href, "_self");
    },
    EditStatus(item) {
      const { href } = this.$router.resolve({
        path: "/scheduleStatusEdit",
        query: {
          data: JSON.stringify(item),
        },
      });
      window.open(href, "_self");
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    try {
      await this.$store.dispatch("getClassroom");
      await this.$store.dispatch("getCourse");
      await this.$store.dispatch("forSchedule");
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./courseInfoManagement.html"></template>
